<template>
  <v-app id="inspire">
  <v-snackbar
     v-model="snackbar"
     :multi-line="true"
     :timeout="6000"
     :top="true"
   >
     {{ this.text_notify }}
     <v-btn
       color="pink"
       flat
       @click="snackbar = false"
     >
       Close
     </v-btn>
   </v-snackbar>
  <span v-if="this.user.grade.type==1">
  <v-dialog
       ref="dialog"
       v-model="show_day_lessons_model"
       persistent
       lazy
       full-width
       width="80%"
     >
                <v-expansion-panel>
        <draggable class="v-expansion-panel theme--light" :move="moveLesson"       v-for="day in this.days" v-if="day.id==show_day_lessons">
     <v-expansion-panel-content
       v-for="(lesson, i) in day.lessons"
       :key="i"
     >
       <div slot="header">{{lesson.name}}</div>
       <v-card>
         <v-card-option class="grey lighten-3"><v-btn color="success" v-if="lesson.status == 3" flat>Odwoane</v-btn>
             <v-btn color="success" @click="ClickSetLessonStatus(3,lesson)" v-else>Odwoane</v-btn>
          <v-btn color="primary" v-if="lesson.status == 1"  flat>Normalnie</v-btn>
               <v-btn color="primary" @click="ClickSetLessonStatus(1,lesson)" v-else>Normalnie</v-btn>
          <v-btn color="error" v-if="lesson.status == 2"  flat>Zastpstwo</v-btn>
          <v-btn color="error" @click="ClickSetLessonStatus(2,lesson)" v-else>Zastpstwo</v-btn>
           <v-btn color="error"  @click="DeleteLesson(lesson)" icon><v-icon>delete</v-icon></v-btn></v-card-option>
           <v-card-body>
           <v-list-tile
avatar
@click="AddEvent(day)"
>
<v-list-tile-content >
<v-list-tile-title>Dodaj wydarzenie</v-list-tile-title>
</v-list-tile-content>
</v-list-tile>
<v-list-tile v-for="event in day.events" v-if="IsEventShow(event,day)" @click="DeleteEvent(event)"
avatar
>
<v-list-tile-content >
<v-list-tile-title>{{event.name}}</v-list-tile-title>
</v-list-tile-content>
</v-list-tile>
           </v-card-body>
       </v-card>
     </v-expansion-panel-content>
   </draggable>
           </v-expansion-panel>
   <v-spacer></v-spacer>
   <v-layout white>
   <v-flex xs6>
   <v-btn flat block color="primary" @click="()=>{show_day_lessons=false;show_day_lessons_model=false;}">ANULUJ</v-btn>
   </v-flex>
   <v-flex xs6>
      <v-btn flat block color="error" @click="()=>{show_day_lessons=false;show_day_lessons_model=false;SaveOrderChange();}">ZAPISZ ZMIANY</v-btn>
   </v-flex>
   </v-layout>
     </v-dialog>
  <v-layout row justify-center>
     <v-dialog v-model="dialog_event_date" persistent max-width="1000px">
       <v-card>
         <v-card-title>
           <span class="headline">Ustaw godziny wydarzenia</span>
         </v-card-title>
         <v-card-text>
           <v-container grid-list-md>
              <v-subheader>Od</v-subheader>
                      <v-time-picker
               full-width
               landscape
               type="month"
               class="mt-3"
                  v-model="event.start_date"
             ></v-time-picker>
                <v-subheader>Do</v-subheader>
             <v-time-picker
                 full-width
                 landscape
                 type="month"
                 class="mt-3"
                 v-model="event.end_date"
               ></v-time-picker>
           </v-container>
         </v-card-text>
         <v-card-actions>
           <v-btn color="orange" dark @click.native="CreateEvent()" block>Utwrz wydarzenie</v-btn>
         </v-card-actions>
       </v-card>
     </v-dialog>
   </v-layout>
  <v-layout row justify-center>
   <v-dialog v-model="dialog_give_badges" fullscreen hide-overlay transition="dialog-bottom-transition">
     <v-card>
       <v-toolbar dark color="orange">
         <v-btn icon dark @click.native="dialog_give_badges =  false">
           <v-icon>close</v-icon>
         </v-btn>
             <v-toolbar-title>Dodaj odznake</v-toolbar-title>
       </v-toolbar>
       <v-list three-line subheader>
       <v-container grid-list-md text-xs-center>
           <v-layout row wrap>
             <v-flex xs4 v-for="badge in this.badges" @click="SelectBadge(badge)">
               <v-card dark color="orange">
               <v-img
          v-bind:src="badge.avatar"
          aspect-ratio="1"
        ></v-img>
               </v-card>
             </v-flex>
             </v-layout>
             </v-container>
       </v-list>
     </v-card>
   </v-dialog>
 </v-layout>
 <v-layout row justify-center>
  <v-dialog v-model="dialog_teacher" fullscreen hide-overlay transition="dialog-bottom-transition">
    <v-card>
      <v-toolbar dark color="orange">
        <v-btn icon dark @click.native="dialog_teacher =  false">
          <v-icon>close</v-icon>
        </v-btn>
            <v-toolbar-title>Dodaj nauczyciela</v-toolbar-title>
      </v-toolbar>
      <v-list three-line subheader>
      <v-container grid-list-md text-xs-center>
      <v-text-field
        label="Imi i nazwisko"
        solo
        v-model="teacher.full_name"
      ></v-text-field>
      <v-select
           :items="user.grade.schedule[0].rooms"
           label="Domylna sala lekcyjna"
           :item-text="item => item.name"
           :item-value="item => item.id"
           style="margin-top:2%;"
           v-model="teacher.default_room_id"
           solo
         ></v-select>
         <v-btn color="orange" block dark @click="CreateTeacher()">Utwrz naucziela</v-btn>
            </v-container>
      </v-list>
    </v-card>
  </v-dialog>
</v-layout>
<v-layout row justify-center>
 <v-dialog v-model="dialog_room" fullscreen hide-overlay transition="dialog-bottom-transition">
   <v-card>
     <v-toolbar dark color="orange">
       <v-btn icon dark @click.native="dialog_room =  false">
         <v-icon>close</v-icon>
       </v-btn>
           <v-toolbar-title>Dodaj sale lekcyjn</v-toolbar-title>
     </v-toolbar>
     <v-list three-line subheader>
     <v-container grid-list-md text-xs-center>
     <v-text-field
       label="Nazwa"
       solo
       v-model="room.name"
     ></v-text-field>
     <v-text-field
       label="Numer sali"
       solo
       type="number"
       v-model="room.number"
       style="margin-top:2%;"
     ></v-text-field>
        <v-btn color="orange" block dark @click="CreateRoom()">Utwrz sale lekcyjn</v-btn>
           </v-container>
     </v-list>
   </v-card>
 </v-dialog>
</v-layout>
  <v-layout row justify-center>
   <v-dialog v-model="dialog_lesson" fullscreen hide-overlay transition="dialog-bottom-transition">
     <v-card>
       <v-toolbar dark color="orange">
         <v-btn icon dark @click.native="dialog_lesson = false">
           <v-icon>close</v-icon>
         </v-btn>
         <v-toolbar-title>Dodaj lekcje</v-toolbar-title>
         <v-spacer></v-spacer>
       </v-toolbar>
       <v-list three-line subheader>
       <v-container>
       <v-text-field
        label="Nazwa"
        solo
        v-model="lesson.name"
      ></v-text-field>
       <v-select
               :items="user.grade.schedule[0].rooms"
               label="Wybierz sale lekcyjn"
               :item-text="item => item.name"
               :item-value="item => item.id"
               solo
                 v-model="lesson.room_id"
                        style="margin-top:2%;"
             ></v-select>
       <v-select
               :items="user.grade.schedule[0].teachers"
               label="Wybierz naucziela"
               :item-text="item => item.full_name"
                  :item-value="item => item.id"
               solo
                 v-model="lesson.teacher_id"
               style="margin-top:2%;"
             ></v-select>
             <v-btn color="orange" block style="margin-top:2%;" dark @click="CreateLesson()">Dodaj lekcj</v-btn>
             </v-container>
       </v-list>
     </v-card>
   </v-dialog>
 </v-layout>
 <v-layout row justify-center>
   <v-dialog v-model="dialog_admin" fullscreen hide-overlay transition="dialog-bottom-transition">
     <v-card>
       <v-toolbar dark color="orange">
         <v-btn icon dark @click.native="dialog_admin = false">
           <v-icon>close</v-icon>
         </v-btn>
         <v-toolbar-title>Panel administracyjny</v-toolbar-title>
         <v-spacer></v-spacer>
       </v-toolbar>
       <v-list three-line subheader>
       <v-tabs
           v-model="active"
           color="orange"
           dark
           slider-color="yellow"
         >
           <v-tab
             ripple
           >
Uytkownicy
           </v-tab>
           <v-tab-item
           >
             <v-subheader>Uytkownicy</v-subheader>
            <template v-for="user in this.user.grade.users">
          <v-list-tile
            avatar
          >
            <v-list-tile-avatar>
              <img :src="user.avatar">
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title v-html="user.full_name"></v-list-tile-title>
            </v-list-tile-content>
            <v-btn color="primary" @click="CreateNotification(user)">Wylij wiadomoc</v-btn>
                <v-btn color="warning" @click="GenerateUser(user)">Wygneruj dane logowania</v-btn>
          <v-btn color="success" @click="LockUser(user)" v-if="user.lock_id==null">Zablokuj</v-btn>
              <v-btn color="error" @click="UnLockUser(user)" v-else>Odblokuj</v-btn>
              <v-btn color='orange'  @click="()=>{select_user = user;dialog_user_edit = true;}" dark>Edytuj</v-btn>
          <v-btn color='error'  @click="DeleteUser(user)">Skasuj</v-btn>
          </v-list-tile>
        </template>

           </v-tab-item>
           <v-tab
             ripple
           >
Zdobyte odznaki
           </v-tab>

           <v-tab v-if="this.user.grade.type==1"
             ripple
           >
Plan zaj
           </v-tab>
           <v-tab-item
           >
           <v-subheader>Zdobyte odznaki</v-subheader>
          <template v-for="user in this.user.grade.users">
        <v-list-tile @click="()=>{select_user_id = user.id;dialog_give_badges = true;}"
          avatar
        >
          <v-list-tile-avatar>
            <img :src="user.avatar">
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title v-html="user.full_name"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
           </v-tab-item>
           <v-tab-item
           v-if="this.user.grade.type==1"
           >
           <v-subheader>Plan zaj</v-subheader>
           <v-btn color="orange" dark @click="CreateDay()">Dodaj dzie</v-btn>
              <v-btn color="error" v-if="save_order_update_day" @click="SaveOrderChangeDay()">Zapisz zmiany pozycji</v-btn>
                            <draggable v-model="days"  :move="moveDay" class="v-list__container">
                            <v-list-tile
                                    v-for="day in this.days"
                                      avatar
                                      @click=""
                                    >
                                      <v-list-tile-content>
                                        <v-list-tile-title v-html="day.name"></v-list-tile-title>
                                      </v-list-tile-content>
       <v-btn flat icon   v-if="day.lessons.length > 0" @click="()=>{show_day_lessons = day.id;show_day_lessons_model = true;}"><v-icon>expand_less</v-icon></v-btn>
         <v-btn color="success" @click="()=>{lesson.day_id = day.id;dialog_lesson = true;}" icon><v-icon>add</v-icon></v-btn>
    <v-btn color="error" @click="DeleteDay(day)" icon><v-icon>delete</v-icon></v-btn>
                                    </v-list-tile>
               </draggable>
           </v-tab-item>
           <v-tab
             ripple
           >
Nauczyciele
           </v-tab>
           <v-tab-item
           >
                <v-btn color="orange" dark @click="()=>{dialog_teacher = true;}">Dodaj nauczyciela</v-btn>
           <v-subheader>Nauczyciele</v-subheader>
          <template v-for="teacher in this.user.grade.schedule[0].teachers">
        <v-list-tile @click="DeleteTeacher(teacher)"
        >
          <v-list-tile-content>
            <v-list-tile-title v-html="teacher.full_name"></v-list-tile-title>
             <v-list-tile-sub-title>Domylna sala: {{teacher.room.number}}</v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
      <v-container fluid grid-list-lg>
   </v-container>
           </v-tab-item>
           <v-tab
             ripple
           >
Sale Lekcyjne
           </v-tab>
           <v-tab-item
           >
             <v-btn color="orange" dark @click="()=>{dialog_room = true;}">Dodaj sale lekcyjn</v-btn>
           <v-subheader>Sale lekcyjne</v-subheader>
          <template v-for="room in this.user.grade.schedule[0].rooms">
        <v-list-tile
        @click="DeleteRoom(room)"
        >
          <v-list-tile-content>
            <v-list-tile-title v-html="room.name"></v-list-tile-title>
             <v-list-tile-sub-title v-html="room.number"></v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
           </v-tab-item>
           <v-tab
             ripple
           >
Informacje
           </v-tab>
           <v-tab-item
           >
          <v-container>
          <v-text-field
            label="Nazwa"
            solo
            v-model="this.user.grade.name"
          ></v-text-field>
          <v-text-field style="margin-top:2%;"
            label="Opis"
            solo
            v-model="this.user.grade.description"
          ></v-text-field>
          <v-btn color="orange" dark block @click="UpdateGrade()">Zapisz zmiany</v-btn>
  <v-btn color="orange" dark block @click="GenerateGrade()">Wygeneruj klucz</v-btn>
  <v-subheader>
          Kolor
           </v-subheader>
  <v-layout>
  <v-flex xs1 @click="()=>{this.user.grade.color = '#d31316';UpdateColorGrade()}" >
  <v-card dark color="secondary">
           <v-card-text style="background-color:#d31316;"></v-card-text>
         </v-card>
    </v-flex>
    <v-flex xs1 @click="()=>{this.user.grade.color = '#0957e8';UpdateColorGrade()}">
    <v-card dark color="secondary">
             <v-card-text style="background-color:#0957e8;"></v-card-text>
           </v-card>
      </v-flex>
      <v-flex xs1  @click="()=>{this.user.grade.color = '#168e04';UpdateColorGrade()}">
      <v-card dark color="secondary" >
               <v-card-text style="background-color:#168e04;"></v-card-text>
             </v-card>
        </v-flex>
        <v-flex xs1 @click="()=>{this.user.grade.color = '#d6bb11';UpdateColorGrade()}">
        <v-card dark color="secondary" >
                 <v-card-text style="background-color:#d6bb11;"></v-card-text>
               </v-card>
          </v-flex>
          <v-flex xs1 @click="()=>{this.user.grade.color = '#a811d6';UpdateColorGrade()}">
          <v-card dark color="secondary" >
                   <v-card-text style="background-color:#a811d6;"></v-card-text>
                 </v-card>
            </v-flex>
            <v-flex xs1  @click="()=>{this.user.grade.color = '#FF420E';UpdateColorGrade()}">
            <v-card dark color="secondary">
                     <v-card-text style="background-color:#FF420E;"></v-card-text>
                   </v-card>
              </v-flex>
              <v-flex xs1  @click="()=>{this.user.grade.color = '#89DA59';UpdateColorGrade()}">
              <v-card dark color="secondary">
                       <v-card-text style="background-color:#89DA59;"></v-card-text>
                     </v-card>
                </v-flex>
                <v-flex xs1 @click="()=>{this.user.grade.color = '#2A3132';UpdateColorGrade()}">
                <v-card dark color="secondary" >
                         <v-card-text style="background-color:#2A3132;"></v-card-text>
                       </v-card>
                  </v-flex>
                  <v-flex xs1 @click="()=>{this.user.grade.color = '#2A3132';UpdateColorGrade()}">
                  <v-card dark color="secondary" >
                           <v-card-text style="background-color:#336B87;"></v-card-text>
                         </v-card>
                    </v-flex>
                    <v-flex xs1 @click="()=>{this.user.grade.color = '#A43820';UpdateColorGrade()}">
                    <v-card dark color="secondary" >
                             <v-card-text style="background-color:#A43820;"></v-card-text>
                           </v-card>
                      </v-flex>
                      <v-flex xs1  @click="()=>{this.user.grade.color = '#0757B';UpdateColorGrade()}">
                      <v-card dark color="secondary" >
                               <v-card-text style="background-color:#0757B;"></v-card-text>
                             </v-card>
                        </v-flex>
                        <v-flex xs1  @click="()=>{this.user.grade.color = '#486B00';UpdateColorGrade()}">
                        <v-card dark color="secondary">
                                 <v-card-text style="background-color:#486B00;"></v-card-text>
                               </v-card>
                          </v-flex>
                          <v-flex xs1 @click="()=>{this.user.grade.color = '#021C1E';UpdateColorGrade()}">
                          <v-card dark color="secondary">
                                   <v-card-text style="background-color:#021C1E;"></v-card-text>
                                 </v-card>
                            </v-flex>
  </v-layout>
                    </v-container>
           </v-tab-item>
         </v-tabs>
       </v-list>
     </v-card>
   </v-dialog>
 </v-layout>
 </span>
 <v-layout row justify-center>
 <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
   <v-card>
     <v-toolbar dark color="orange">
       <v-btn icon dark @click.native="dialog = false">
         <v-icon>close</v-icon>
       </v-btn>
       <v-toolbar-title>Wylosuj szczliwy numerek</v-toolbar-title>
       <v-spacer></v-spacer>
       <v-toolbar-items>
       </v-toolbar-items>
     </v-toolbar>
     <v-list three-line subheader>
     <v-layout row py-4 justify-center>
        <v-flex xs12 md6 >
     <v-card>
            <v-card-title primary-title >
                     <v-layout row  justify-center>
                     <h1 style="font-size:240px;">{{this.lucky_number}}</h1>
                  </v-layout>
     </v-card-title>
     </v-card>
        </v-flex>
     </v-layout>
     </v-list>
   </v-card>
 </v-dialog>
</v-layout>
  <v-navigation-drawer id="nav-2"
     v-model="drawer"
     absolute
     temporary
   >
     <v-list class="pa-1">
       <v-list-tile avatar>
         <v-list-tile-avatar>
           <img v-bind:src="this.user.avatar">
         </v-list-tile-avatar>

         <v-list-tile-content>
           <v-list-tile-title>{{this.user.full_name}}</v-list-tile-title>
         </v-list-tile-content>
         <v-list-tile-content>
          <v-btn flat icon @click="$router.push({name:'UserEdit',params:{}})"><v-icon>settings</v-icon></v-btn>
                   </v-list-tile-content>
       </v-list-tile>
     </v-list>

     <v-list class="pt-0" dense>
     <v-list-tile
       @click="()=>{$router.push({name:'UserNotifications',params:{}})}"
     >
       <v-list-tile-action>
         <v-icon>mail</v-icon>
       </v-list-tile-action>

       <v-list-tile-content>
         <v-list-tile-title>Powiadomienia <v-badge>{{notifications_length}}</v-badge></v-list-tile-title>
       </v-list-tile-content>
     </v-list-tile>
       <v-divider></v-divider>
       <v-list-tile
         @click=""
       >
         <v-list-tile-action>
           <v-icon>class</v-icon>
         </v-list-tile-action>

         <v-list-tile-content>
           <v-list-tile-title>Twoje klasy</v-list-tile-title>
         </v-list-tile-content>
       </v-list-tile>
       <v-list-tile v-for="grade in this.user.school.grades"
         @click="SelectGrade(grade.id)"
         v-if="grade.type==1 && (user.role.id > 4 || grade.user_id == user.id)"
       >
         <v-list-tile-action>
           <v-icon>class</v-icon>
         </v-list-tile-action>

         <v-list-tile-content>
           <v-list-tile-title>{{grade.name}}</v-list-tile-title>
         </v-list-tile-content>
         </v-list-title>
       </v-list-tile>
       <v-list-tile
         @click=""
         v-if="this.above_premium"
       >
         <v-list-tile-action>
           <v-icon>class</v-icon>
         </v-list-tile-action>

         <v-list-tile-content v-if="this.above_premium">
           <v-list-tile-title>Twoje samorzdy</v-list-tile-title>
         </v-list-tile-content>
       </v-list-tile>
       <v-list-tile v-for="grade in this.user.school.grades"
         @click="SelectGrade(grade.id)"
         v-if="above_premium && grade.type == 2 && (user.role.id > 4 || grade.user_id == user.id)"
       >
         <v-list-tile-action>
           <v-icon>class</v-icon>
         </v-list-tile-action>

         <v-list-tile-content>
           <v-list-tile-title>{{grade.name}}</v-list-tile-title>
         </v-list-tile-content>
         </v-list-title>
       </v-list-tile>
       <v-list-tile
         v-if="this.user.role_id > 2 && this.user.grade.type==1"
         @click="()=>{dialog_admin = true;}"
       >
         <v-list-tile-action>
           <v-icon>settings</v-icon>
         </v-list-tile-action>

         <v-list-tile-content>
           <v-list-tile-title>Panel Administracyjny</v-list-tile-title>
         </v-list-tile-content>
       </v-list-tile>
       <v-list-tile
         @click="Logout()"
       >
         <v-list-tile-action>
           <v-icon>exit_to_app</v-icon>
         </v-list-tile-action>

         <v-list-tile-content>
           <v-list-tile-title>Wyloguj</v-list-tile-title>
         </v-list-tile-content>
       </v-list-tile>
     </v-list>
   </v-navigation-drawer>
    <v-toolbar color="orange" dark  clipped-left id="nav">
      <v-toolbar-side-icon @click.native="drawer = !drawer"></v-toolbar-side-icon>
      <v-btn icon @click="()=>{$router.push({name:'GradeIndex',props:[]})}">
       <v-icon>add_to_home_screen</v-icon>
     </v-btn>
