     GetThisDayDate(day){
       let day_name = "";
       switch(day.day_number){
         case 1:
         day_name = "Monday";
         break;
         case 2:
           day_name = "Tuesday";
         break;
         case 3:
           day_name = "Wednesday";
         break;
         case 4:
           day_name = "Thursday";
         break;
         case 5:
           day_name = "Friday";
         break;
         case 6:
           day_name = "Saturday";
         break;
         case 7:
           day_name = "Sunday";
         break;
       }
      let date = moment(this.date).day(day_name).format('YYYY-MM-DD');
      return date;
     },
     IsEventShow(event,day){
       let day_name = "";
       switch(day.day_number){
         case 1:
         day_name = "Monday";
         break;
         case 2:
           day_name = "Tuesday";
         break;
         case 3:
           day_name = "Wednesday";
         break;
         case 4:
           day_name = "Thursday";
         break;
         case 5:
           day_name = "Friday";
         break;
         case 6:
           day_name = "Saturday";
         break;
         case 7:
           day_name = "Sunday";
         break;
       }
           let date = moment(this.date).day(day_name).format('YYYY-MM-DD');
      let startDate = moment(event.start_date);
      return date == startDate.format('YYYY-MM-DD');
     },
     getEventTimeStart(event){
       let date;
       if(moment(event.start_date).format('m')<10){
             date = moment(event.start_date).format('H:0m:s')
       }
       if(moment(event.start_date).format('s')<10 && moment(event.start_date).format('m')<10){
           date = moment(event.start_date).format('H:0m:0s');
       }
       if(moment(event.start_date).format('s')<10){
           date = moment(event.start_date).format('H:0m:0s');
         }

       return date;
     },
     getEventTimeEnd(event){
     let date;
     if(moment(event.end_date).format('m')<10){
           date = moment(event.end_date).format('H:0m:s')
     }
     if(moment(event.end_date).format('s')<10 && moment(event.end_date).format('m')<10){
         date = moment(event.end_date).format('H:0m:0s');
     }
     if(moment(event.end_date).format('s')<10){
         date = moment(event.end_date).format('H:0m:0s');
       }

     return date;
     },
