<template>
<v-app>
        <v-carousel v-if="this.open_gallery !=false && this.open_gallery.images.length > 1" style="height: 100vh;position:fixed;z-index:99999999999999999999;">
        <v-btn flat icon style="z-index:99999999999999999999999999999999999999999;float:right;" @click="()=>{this.open_gallery = false;}"><v-icon>close</v-icon></v-btn>
           <v-carousel-item  transition="none"
             v-for="(item,i) in this.open_gallery.images"
             :key="i"
             :src="item"
           ></v-carousel-item>
         </v-carousel>
         <v-carousel v-else-if="this.open_gallery !=false" style="height: 100vh;position:fixed;z-index:99999999999999999999;" hide-delimiters hide-controls>
         <v-btn flat icon style="z-index:99999999999999999999999999999999999999999;float:right;" @click="()=>{this.open_gallery = false;}"><v-icon>close</v-icon></v-btn>
                   <v-btn flat icon  style="z-index:99999999999999999999999999999999999999999;float:right;" :href="this.open_gallery.images[0]"><v-icon>cloud_download</v-icon></v-btn>
            <v-carousel-item  transition="none"
              v-for="(item,i) in this.open_gallery.images"
              :key="i"
              :src="item"
            ></v-carousel-item>
          </v-carousel>
<span v-if="writing==false">
<v-list subheader>
         <v-subheader>Czat</v-subheader>
         <v-list-tile avatar v-for="user in this.users" v-if="user.id  !=userId">
           <v-list-tile-avatar>
             <img v-bind:src="user.avatar">
           </v-list-tile-avatar>

           <v-list-tile-content>
             <v-list-tile-title>{{user.full_name}}</v-list-tile-title>
           </v-list-tile-content>

           <v-list-tile-action>
           <v-btn style="border:none;background:none;box-shadow:none;" @click="writing_with(user)">
             <v-icon color="orange">chat_bubble</v-icon>
             </v-btn>
           </v-list-tile-action>
         </v-list-tile>
       </v-list>
       </span>
       <span v-else>
       <v-container style="line-height: 70%;margin-bottom:20%;">
       <v-container grid-list-xl>
       <v-layout v-bind="binding">
              <v-flex xs12>
<span v-for="message in this.messages" v-bind:id="'m'+message.id" style="margin-bottom:10%;">
<span v-if="user.id == message.written_id">
<v-flex xs6>
<v-layout v-bind="binding">
<v-flex xs12>
           <v-card dark  color="primary" style="border-top-right-radius: 1.3em;border-top-left-radius: 1.3em;border-bottom-right-radius: 1.3em;border-bottom-left-radius: 1.3em;">
                           <v-img v-if="message.images[0]" @click="()=>{open_gallery = message}"
                    v-bind:src="message.images[0]"
                    aspect-ratio="2.75"
                  ></v-img>

                 <v-card-text @click="DeleteMessage(message)" v-html="message.content"></v-card-text>
                 <span v-if="message.files[0]">
                 <v-card-actions   v-for="file in message.files">
        <v-btn flat v-bind:href="file" color="orange">{{file}}</v-btn>
        </v-card-actions>
        </span>
                  </v-card>
                  </v-flex>
                  </v-layout>
                  </v-flex>
</span>
<span v-else>
<v-layout row justify-right>
        <v-flex xs6>
        </v-flex>
                    <v-flex xs6>
                   <v-card dark color="secondary" style="border-top-right-radius: 1.3em;border-top-left-radius: 1.3em;border-bottom-right-radius: 1.3em;border-bottom-left-radius: 1.3em;">
                   <v-img v-if="message.images[0]" @click="()=>{open_gallery = message}"
            v-bind:src="message.images[0]"
            aspect-ratio="2.75"
          ></v-img>

         <v-card-text v-html="message.content"></v-card-text>
         <span v-if="message.files[0]">
         <v-card-actions   v-for="file in message.files">
<v-btn flat v-bind:href="file" color="orange">{{file}}</v-btn>
</v-card-actions>
</span>
                          </v-card>
                          </v-flex>
                          </v-layout>
</span>
</span>
              </v-flex>
              </v-layout>
              </v-container>
              </v-container>
         </v-card>
       </v-flex>
      </v-layout>
      <v-container fluid style="  left: 0;
    bottom: 0;
    width: 100%;
    position:fixed;
 ">
 <v-layout row wrap>
          <v-flex
            xs1
            d-flex
            v-for="image in this.images"
          >
            <v-card flat tile class="d-flex">
              <v-img
                :src="image"
                :lazy-src="image"
                aspect-ratio="1"
                class="grey lighten-2"
                @click="DeleteImage(image)"
              >
                <v-layout
                  slot="placeholder"
                  fill-height
                  align-center
                  justify-center
                  ma-0
                >
                  <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                </v-layout>
              </v-img>
            </v-card>
          </v-flex>
        </v-layout>
      <v-textarea
      v-model="message.content"
        solo
        label="Tre"
      ></v-textarea>
               <div class="upload-btn-wrapper">
            <v-btn flat><v-icon>photos</v-icon></v-btn>
             <input type="file" name="myfile"  accept="image/*" multiple @change='this.OnFileChangeMessage'>
            </div>
            <div class="upload-btn-wrapper">
         <v-btn flat><v-icon>attachment</v-icon></v-btn>
          <input type="file" name="myfile"   multiple @change='this.OnFileChangeMessageFile'>
         </div>
         <v-btn block color="orange" @click="CreateMessage()"  :loading="this.wait"
      :disabled="this.wait"
      class="white--text">Wylij wiadomo</v-btn>
         </v-card-text>
         </v-card>
         </v-container>
      </v-container>
       </span>
  </v-app>
</template>
<script>
  export default {
  data(){
  return {
  user:{},
  userId:null,
  users:[],
  message:{
  content:""
  },
  select_user:{},
  messages:[],
  writing:false,
  images:[],
  wait:false,
  open_gallery:false,
  files:[]
  }
  },
  watch:{
  messages:function(){
 setTimeout(()=>{
 this.$vuetify.goTo("#m"+this.messages[this.messages.length-1].id,{
         duration:300,
         offset:100,
         easing:"linear"
       });
 },1);
 },
open_gallery:function() {
 let x = document.getElementById("nav");
if(this.open_gallery !=false){
x.style.display = "none";
}else{
                x.style.display = "block";
}
let x2 = document.getElementById("nav-2");
if(this.open_gallery !=false){
x2.style.display = "none";
}else{
               x2.style.display = "block";
}
}
},
  methods:{
  OnFileChangeMessage(e){
let files = e.target.files || e.dataTransfer.files;
var i;
for (i = 0; i < files.length; i++) {
this.createImg(files[i]);
}
},
createImg(file){
var image = new Image;
var reader = new FileReader;
reader.onload = (e) => {
this.wait = true;
axios.post("image",{image:e.target.result}).then((response)=>{
this.images.push(response.data.data.image);
this.wait = false;
});
};
reader.readAsDataURL(file);
},
OnFileChangeMessageFile(e){
let files = e.target.files || e.dataTransfer.files;
var i;
for (i = 0; i < files.length; i++) {
this.createFile(files[i]);
}
},
DeleteImage(image){
this.images.splice(this.images.indexOf(image),1);
},
createFile(file){
var reader = new FileReader;
reader.onload = (e) => {
let type = file["name"].split(".")[1];
this.wait = true;
axios.post("file",{file:e.target.result,type:type}).then((response)=>{
this.files.push(response.data.data.file);
this.wait = false;
});
};
reader.readAsDataURL(file);
},
DeleteImagePost(image){
this.imagesPost.pop(image);
},
writing_with(user){
this.writing = true;
this.select_user = user;
axios.get("messages/user/"+user.id).then((response)=>{
this.messages = response.data.data;
});
},
DeleteMessage(message){
if(confirm("Czy na pewno chcesz usun ?")){
axios.delete('messages/'+message.id).then((response)=>{
axios.get("messages/user/"+this.select_user.id).then((response)=>{
this.messages = response.data.data;
});
});
}
},
CreateMessage(){
axios.post('messages',{content:this.message.content,user_id:this.select_user.id,images:this.images,files:this.files}).then((response)=>{
this.message.content = "";
this.images = [];
this.files = [];
axios.get("messages/user/"+this.select_user.id).then((response)=>{
this.messages = response.data.data;
});
});
}
  },
     created(){
     let response = [];
    response['data'] = JSON.parse(localStorage.getItem('me'));
     this.user = response.data
  this.posts = response.data.grade.posts;
     this.user = response.data
     this.userId = response.data.id;
if(this.user.grade.type == 2){
axios.get('grades/'+this.user.grade.id+'/users').then((response)=>{
this.users = response.data.data;
});
}else{
this.users= response.data.grade.users;
}
if(this.$route.params.user !=undefined)
{
axios.get('users/'+this.$route.params.user).then((response)=>{
this.writing_with(response.data.data);
});
}
     },
     mounted(){
     Echo.private('message')
    .listen('MessageCreatedEvent',(e)=>{
    axios.get("messages/user/"+this.select_user.id).then((response)=>{
    this.messages = response.data.data;
    });
    Echo.private('message')
    .listen('MessageDeletedEvent',(e)=>{
    axios.get("messages/user/"+this.select_user.id).then((response)=>{
    this.messages = response.data.data;
    });
        //document.querySelector('#message_end').scrollIntoView({
        // behavior: 'smooth'
        //});
      });
    });
     }
  }
  </script>
  <style scoped>
  .upload-btn-wrapper {
    position: relative;
    overflow: hidden;
    display: inline-block;
  }
  .btn {
    border: 2px solid gray;
    color: gray;
    background-color: white;
    padding: 8px 20px;
    border-radius: 8px;
    font-size: 20px;
    font-weight: bold;
  }

  .upload-btn-wrapper input[type=file] {
    font-size: 100px;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
  }
  </style>
