<template>
<v-app id="inspire">
  <v-toolbar color="orange" dark  clipped-left id="nav">
    <v-icon @click="$router.push({name:'GradeIndex',params:{}})">keyboard_backspace</v-icon>
    <v-toolbar-title class="white--text">Powiadomienia</v-toolbar-title>
    <v-spacer></v-spacer>
  </v-toolbar>
  <v-content style="background-color:white;">
   <v-subheader>Twoje powiadomienia ({{notifications.length}})</v-subheader>
   <v-list three-line>
         <template v-for="(notification, index) in notifications">
           <v-list-tile
             avatar
             @click=""
           >

             <v-list-tile-content>
               <v-list-tile-title v-html="notification.data.content"></v-list-tile-title>
             </v-list-tile-content>
           </v-list-tile>
         </template>
       </v-list>
  </v-content>
</v-app>
</template>
<script>
  export default {
  data(){
return {
user:{},
notifications:{}
}
  },
     created(){
     let response = [];
    response['data'] = JSON.parse(localStorage.getItem('me'));
     this.user = response.data
     axios.get("auth/notifications").then((response)=>{
   this.notifications = response.data.data;
 });
      }
}
  </script>
