<template>
<v-app>
<h1 class='font-weight-black  text-xs-center' style='margin-bottom:2%;margin-top:5%;'>Plan zaj</h1>
<v-container>
<div class='text-xs-center' style="margin-bottom:2%;">
<v-btn color="orange" dark @click='BackWeek'><</v-btn>
<v-btn color="orange" dark @click="()=>{this.change_date = true}">Wybierz dat</v-btn>
 <v-btn color="orange" dark @click='NextWeek'>></v-btn>
 </div>
 <v-flex xs12 sm6 md4>
    <v-dialog
      ref="change_date"
      v-model="change_date"
      :return-value.sync="date_modal"
      persistent
      lazy
      full-width
      width="290px"
    >
          <v-date-picker v-model="date_modal" scrollable color="orange">
        <v-spacer></v-spacer>
        <v-btn flat color="orange" @click="change_date = false">Cancel</v-btn>
        <v-btn flat color="orange" @click="ChangeDate()">OK</v-btn>
      </v-date-picker>
    </v-dialog>
  </v-flex>
<v-layout>
   <v-flex v-bind:class="()=>{if(12 / this.days.length < 1){return 'xs2';}else{return 'xs'+12 / this.days.length}}" v-for="day in this.days" style="margin-left:1%;">
   <v-card style="margin-bottom:4%;color:white;"  color="orange">
   <v-container>
   <p class='font-weight-black  text-xs-center'>{{GetThisDayDate(day)}}</p>
<h2 class='font-weight-black  text-xs-center' style='margin-bottom:2%;margin-top:5%;'>{{day.name}}</h2>
<div v-for="lesson in day.lessons" v-bind:style="{backgroundColor:CheckLessonColor(lesson)}">
{{lesson.name}} ({{lesson.room.number}})
</div>
</v-container>
 <v-btn block color="orange" @click='()=>{show_events = true;}' v-if="!show_events" style="color:white;">Poka wydarzenia</v-btn>
  <v-btn block color="orange" @click='()=>{show_events = false;}' v-else style="color:white;">Ukryj wydarzenia</v-btn>
  <div v-for="event in day.events" v-if="show_events">
  <v-container>
  <ol>
  <li v-if="IsEventShow(event,day)">
     {{event.name}} {{getEventTimeStart(event)}} - {{getEventTimeEnd(event)}}
 </li>
 </ol>
 </v-container>
  </div>
   </v-card>
   </v-flex>
   </v-layout>
   </v-container>
  </v-app>
</template>
<script>
export default {
  name:'Schdule',
  data () {
    return {
      'date':new Date(),
      'date_modal':new Date().toISOString().substr(0, 10),
      'days':[],
      show_events:false,
      change_date:false
    }
  },
  mounted(){
  let response = [];
 response['data'] = JSON.parse(localStorage.getItem('me'));
  this.user = response.data
 this.days = response.data.grade.schedule[0].days
  },
  methods:{
    GetThisDayDate(day){
      let day_name = "";
      switch(day.day_number){
        case 1:
        day_name = "Monday";
        break;
        case 2:
          day_name = "Tuesday";
        break;
        case 3:
          day_name = "Wednesday";
        break;
        case 4:
          day_name = "Thursday";
        break;
        case 5:
          day_name = "Friday";
        break;
        case 6:
          day_name = "Saturday";
        break;
        case 7:
          day_name = "Sunday";
        break;
      }
     let date = moment(this.date).day(day_name).format('YYYY-MM-DD');
     return date;
    },
    IsEventShow(event,day){
      let day_name = "";
      switch(day.day_number){
        case 1:
        day_name = "Monday";
        break;
        case 2:
          day_name = "Tuesday";
        break;
        case 3:
          day_name = "Wednesday";
        break;
        case 4:
          day_name = "Thursday";
        break;
        case 5:
          day_name = "Friday";
        break;
        case 6:
          day_name = "Saturday";
        break;
        case 7:
          day_name = "Sunday";
        break;
      }
          let date = moment(this.date).day(day_name).format('YYYY-MM-DD');
     let startDate = moment(event.start_date);
     return date == startDate.format('YYYY-MM-DD');
    },
    getEventTimeStart(event){
      let date;
      if(moment(event.start_date).format('m')<10){
            date = moment(event.start_date).format('H:0m:s')
      }
      if(moment(event.start_date).format('s')<10 && moment(event.start_date).format('m')<10){
          date = moment(event.start_date).format('H:0m:0s');
      }
      if(moment(event.start_date).format('s')<10){
          date = moment(event.start_date).format('H:0m:0s');
        }

      return date;
    },
    getEventTimeEnd(event){
      let date;
      if(moment(event.end_date).format('m')<10){
            date = moment(event.end_date).format('H:0m:s')
      }
      if(moment(event.end_date).format('s')<10 && moment(event.end_date).format('m')<10){
          date = moment(event.end_date).format('H:0m:0s');
      }
      if(moment(event.end_date).format('s')<10){
          date = moment(event.end_date).format('H:0m:0s');
        }

      return date;
    },
    NextWeek(){
        //alert("Pracujemy nad t funkcj....");
  //     let date = moment(this.date).day("Monday").format("YYYY-MM-DD");
  this.date = moment(this.date).add(1,'weeks').format("YYYY-MM-DD");
  },
  ChangeDate(){
  this.date = moment(this.date_modal).format("YYYY-MM-DD");
  this.change_date = false;
  },
  BackWeek(){
    //alert("Pracujemy nad t funkcj....");
    //   let date = moment(this.date).day("Monday").format("YYYY-MM-DD");
    // this.date = moment(date).subtract(this.days.length,'days').format("YYYY-MM-DD");
      this.date = moment(this.date).subtract(1,'weeks').format("YYYY-MM-DD");
  },
  CheckLessonColor(lesson){
  let color;
     if(lesson.status==2){
         color = "red";
     }else if(lesson.status==3){
      color = "green";
     }
     return color;
  }
  }
}
  </script>
