<template>
<div>
<v-carousel v-if="this.open_gallery !=false && this.open_gallery.images.length > 1" style="height: 100vh;position:fixed;z-index:99999999999999999999;top: 0;bottom: 0;left: 0;right: 0;">
<v-btn flat icon style="z-index:99999999999999999999999999999999999999999;float:right;" @click="()=>{this.open_gallery = false;}"><v-icon>close</v-icon></v-btn>
   <v-carousel-item  transition="none"
     v-for="(item,i) in this.open_gallery.images"
     :key="i"
     :src="item"
   ></v-carousel-item>
 </v-carousel>
 <v-carousel v-else-if="this.open_gallery !=false" style="height: 100vh;position:fixed;z-index:99999999999999999999;top: 0;bottom: 0;left: 0;right: 0;" hide-delimiters hide-controls>
 <v-btn flat icon style="z-index:99999999999999999999999999999999999999999;float:right;" @click="()=>{this.open_gallery = false;}"><v-icon>close</v-icon></v-btn>
                <v-btn flat icon  style="z-index:99999999999999999999999999999999999999999;float:right;" :href="this.open_gallery.images[0]"><v-icon>cloud_download</v-icon></v-btn>
    <v-carousel-item  transition="none"
      v-for="(item,i) in this.open_gallery.images"
      :key="i"
      :src="item"
    ></v-carousel-item>
  </v-carousel>
  <v-card light style="margin-bottom:4%;" v-if="post.deleted_at==null">
  <v-img v-if="show_as_image(post)==true">
<div v-html="post.content">
</div>
</v-img>
<v-img v-else-if="post.images.length == 1"
v-bind:src="post.images[0]"
aspect-ratio="2.75"
@click="()=>{open_gallery = this.post}"
></v-img>
<span v-else-if="post.images.length>0" @click="()=>{open_gallery = this.post}">
<v-carousel >
<v-carousel-item  transition="none"
  v-for="(item,i) in post.images"
  :key="i"
  :src="item"
></v-carousel-item>
</v-carousel>
</span>
<video style="width:100%;" controls v-else-if="post.videos.length==1">
<source v-bind:src="post.videos[0]" type="video/mp4">
</video>
    <v-img v-else
      src="/vendor/socialler/img/not_image_this_post.png"
      aspect-ratio="2.75"
    >
   </v-img>

    <v-card-title primary-title>
      <div v-if="show_as_image(post)==false" style="color:black;">
                 <div v-html="post.content"></div>
      </div>
    </v-card-title>
<v-card-options v-for="file in post.files">
<v-btn flat color="dark" v-bind:href="file">{{file}}</v-btn>
</v-card-options>
    <v-card-actions>
     <v-btn-toggle>
           <v-btn flat color="red" style="opacity:1 !important;" v-if="IfTrust(post)" @click="trust(post)"><i class="material-icons">flag</i></v-btn>
                    <v-btn flat color="success" style="opacity:1 !important;"  v-else @click="trust(post)"><i class="material-icons">flag</i></v-btn>
                           <v-btn flat color="dark" style="opacity:1 !important;"  @click="()=>{if(create_comment !=false){create_comment=false;}else{create_comment = post.id}}">Dodaj komentarz</v-btn>
                                     <v-btn flat color="red" style="opacity:1 !important;"  @click="DeletePost(post)" v-if="user.id == post.user_id || user.role.id > 1">Skasuj</v-btn>
                                     </v-btn-toggle>
    </v-card-actions>
           <v-divider></v-divider>
    <v-container v-if="create_comment !=false && create_comment == post.id" fluid>
     <v-text-field
     v-model="comment.content"
  v-validate="'required'"
  label="Tre"
  required
></v-text-field>
  <v-btn block dark color="dark" @click="CreateComment(post)">Dodaj komentarz</v-btn>
</v-container>
<v-container grid-list-xl>
<v-layout v-bind="binding">
       <v-flex>
               <v-card dark color="white" style="background:none;border:none;box-shadow:none;" v-for="comment in post.comments" v-if="comment.deleted_at==null">
               <v-card-text>  <v-avatar color="grey darken-4"><img v-bind:src="comment.user.avatar" height="40" width="40"></v-avatar><span  v-html="comment.content" style="margin-left:2%;"></span>   <v-btn flat color="red" @click="DeleteComment(comment)" v-if="user.id == comment.user_id || user.role.id > 1">Skasuj</v-btn>  <v-btn flat color="dark" @click="()=>{create_answer = !create_answer;}">Dodaj odpowied</v-btn> </v-card-text>
               <v-container v-if="create_answer" fluid>
                <v-text-field
                v-model="answer.content"
             v-validate="'required'"
             label="Tre"
             required
           ></v-text-field>
             <v-btn block dark color="dark" @click="CreateAnswer(comment)">Dodaj odpowied</v-btn>
           </v-container>
               <v-layout v-bind="binding">
                      <v-flex>
                              <v-card dark color="white" style="background:none;border:none;box-shadow:none;margin-left:4%;" v-for="answer in comment.answers" v-if="answer.deleted_at==null">
                                  <v-card-text>  <v-avatar color="grey darken-4"><img v-bind:src="answer.user.avatar" height="40" width="40"></v-avatar><span  v-html="answer.content" style="margin-left:2%;"></span>   <v-btn flat color="red" @click="DeleteAnswer(answer)" v-if="user.id == answer.user_id || user.role.id > 1">Skasuj</v-btn>     </v-card-text>
                  </v-card>
                      </v-flex>
                      </v-layout>
       <v-card-text></v-card-text>
   </v-card>
       </v-flex>
       </v-layout>
       </v-container>
  </v-card>
</div>
</template>
<script>
  export default {
  props:['post','user'],
  data(){
  return {
  comment:{
  content:""
  },
  open_gallery:false,
  answer:{
    content:""
    },
  create_comment:false,
    create_answer:false,
         imagesPost:[]
  }
  },
  watch:{
open_gallery:function() {
 let x = document.getElementById("nav");
if(this.open_gallery !=false){
x.style.display = "none";
}else{
                x.style.display = "block";
}
let x2 = document.getElementById("nav-2");
if(this.open_gallery !=false){
x2.style.display = "none";
}else{
               x2.style.display = "block";
}
}
},
  methods:{
  DeletePost(post){
  axios.delete('posts/'+post.id).then((response)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.$emit('postsUpdate',response.data.grade.posts);
  });
  });
  },
  DeleteComment(comment){
  axios.delete('comments/'+comment.id).then((response)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.$emit('postsUpdate',response.data.grade.posts);
  });
  });
  },
  trust(post){
  axios.post("trusts",{post_id:post.id}).then((response)=>{
  if(response.data.deleted){
  post.trusts.filter((trust)=>{
     if(trust.user_id==this.user.id){
        this.post.trusts.shift(trust);
     }
     });
  }else{
    this.post.trusts.push(response.data.data);
  }
  });
  },
  DeleteAnswer(answer){
  axios.delete('answers/'+answer.id).then((response)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.$emit('postsUpdate',response.data.grade.posts);
  });
  });
  },
  CreateComment(post){
  axios.post("comments",{content:this.comment.content,grade_id:this.user.grade_id,images:[],post_id:post.id}).then((response)=>{
    this.comment.content = "";
    this.create_comment = false;
  axios.post('auth/me?mobile=true').then((response)=>{
  this.$emit('postsUpdate',response.data.grade.posts);
  });
  alert("Poprawnie utworzono komentarz");
  }).catch((error)=>{
  alert("Bd podczas tworzenia komentarza");
  });
  },
  CreateAnswer(comment){
  axios.post("answers",{content:this.answer.content,grade_id:this.user.grade_id,images:[],comment_id:comment.id}).then((response)=>{
    this.answer.content = "";
    this.create_answer = false;
    axios.post('auth/me?mobile=true').then((response)=>{
    this.$emit('postsUpdate',response.data.grade.posts);
    });
  alert("Poprawnie utworzono opdowied");
  }).catch((error)=>{
  alert("Bd podczas tworzenia odpowiedzi");
  });
  },
  IfTrust(post){
     post['trust'] = false;
  post.trusts.filter((trust)=>{
     if(trust.user_id==this.user.id){
     post['trust'] = true;
     post['trust_data'] = trust;
     }
  });
  return  post['trust'];
    },
    show_as_image(post){
    const contains = post.content.includes('content-background');
    return contains;
    }
  }
  }
  </script>