<template>
<v-app>
        <v-dialog
              v-model="dialog_post"
              hide-overlay
              persistent
              width="300"
            >
              <v-card
                color="orange"
                dark
              >
                <v-card-text>
                  Trwa tworzenie postu
                  <v-progress-linear
                    indeterminate
                    color="white"
                    class="mb-0"
                  ></v-progress-linear>
                </v-card-text>
              </v-card>
            </v-dialog>
<v-container>
<v-layout>
   <v-flex xs12 sm6 offset-sm3>
   <v-card light style="margin-bottom:4%;">
   <v-container>
       <v-textarea
       v-model="post.content"
         solo
         label="Jakie notatki dla klasy ?"
       ></v-textarea>
       <div class="upload-btn-wrapper">
          <v-btn flat icon><v-icon>photos</v-icon></v-btn>
           <input type="file" name="myfile"  accept="image/*" multiple @change='this.OnImageChangePost'>
          </div>
          <div class="upload-btn-wrapper">
             <v-btn flat icon><v-icon>video_library</v-icon></v-btn>
              <input type="file" name="myfile"  accept="video/*" multiple @change='this.OnVideoChangePost'>
             </div>
             <div class="upload-btn-wrapper">
          <v-btn flat icon><v-icon>attachment</v-icon></v-btn>
           <input type="file" name="myfile"   multiple @change='this.OnFileChangePost'>
          </div>
      <v-btn block color="orange" @click="CreatePost()" dark  :loading="this.wait"
      :disabled="this.wait"
      class="white--text">Dodaj post</v-btn>
</v-container>
   </v-card>
  <Post v-for="post in this.posts" :post="post" :user="user" v-on:postsUpdate="(e)=>{posts = e;}"></Post>
   </v-flex>
 </v-layout>
 </v-container>
  </v-app>
</template>
<script>
import Post from './Post'
  export default {
  components:{Post},
  data(){
  return {
  comment:{
  content:""
  },
answer:{
  content:""
  },
    post:{
  content:""
  },
  posts:[],
  user:{},
  create_comment:false,
  create_answer:false,
  imagesPost:[],
  e:{},
     dialog_post:false,
     videosPost:[],
      filesPost:[],
      wait:false
  }
  },
  methods:{
  OnFileChangePost(e){
let files = e.target.files || e.dataTransfer.files;
var i;
for (i = 0; i < files.length; i++) {
this.createFilePost(files[i]);
}
},
OnVideoChangePost(e){
let files = e.target.files || e.dataTransfer.files;
var i;
for (i = 0; i < files.length; i++) {
this.createVideoPost(files[i]);
}
},
OnImageChangePost(e){
let files = e.target.files || e.dataTransfer.files;
var i;
for (i = 0; i < files.length; i++) {
this.createImgPost(files[i]);
}
},
createImgPost(file){
var image = new Image;
var reader = new FileReader;
reader.onload = (e) => {
this.wait = true;
axios.post("image",{image:e.target.result}).then((response)=>{
this.imagesPost.push(response.data.data.image);
this.wait = false;
});
};
reader.readAsDataURL(file);
},
createVideoPost(file){
var reader = new FileReader;
reader.onload = (e) => {
this.wait = true;
axios.post("video",{video:e.target.result}).then((response)=>{
this.videosPost.push(response.data.data.video);
this.wait = false;
});
};
reader.readAsDataURL(file);
},
createFilePost(file){
var reader = new FileReader;
reader.onload = (e) => {
this.wait = true;
let type = file["name"].split(".")[1];
axios.post("file",{file:e.target.result,type:type}).then((response)=>{
this.filesPost.push(response.data.data.file);
this.wait = false;
});
};
reader.readAsDataURL(file);
},
  CreatePost(){
this.dialog_post = true;
  axios.post("posts",{content:"#notes_from_lesson "+this.post.content,grade_id:this.user.grade_id,images:this.imagesPost,videos:this.videosPost,files:this.filesPost}).then((response)=>{
    this.post.content = "";
        this.imagesPost = [];
            this.filesPost = [];
              this.videosPost = [];
  axios.post('auth/me?mobile=true').then((response)=>{
  this.user = response.data
  localStorage.setItem('me', JSON.stringify(response.data));
 axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
this.posts = response.data.data;
 });
  });
  this.dialog_post = false;
  alert("Poprawnie utworzono post");
  }).catch((error)=>{
    this.dialog_post = false;
  alert("Bd podczas tworzenia postu");
  });
  }
  },
     created(){
     let response = [];
    response['data'] = JSON.parse(localStorage.getItem('me'));
     this.user = response.data
    axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
  this.posts = response.data.data;
    });
     },
     mounted(){
     Echo.private('posts')
  .listen('PostCreatedEvent',(e)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.user = response.data
  localStorage.setItem('me', JSON.stringify(response.data));
 axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
this.posts = response.data.data;
 });
  });
  });
  Echo.private('posts')
  .listen('PostDeletedEvent',(e)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.user = response.data
 axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
this.posts = response.data.data;
 });
  });
  });
  Echo.private('comments')
  .listen('CommentDeletedEvent',(e)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.user = response.data
 axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
this.posts = response.data.data;
 });
  });
  });
  Echo.private('comments')
  .listen('CommentCreatedEvent',(e)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.user = response.data
 axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
this.posts = response.data.data;
 });
  });
  });
  Echo.private('answers')
  .listen('AnswerCreatedEvent',(e)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.user = response.data
 axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
this.posts = response.data.data;
 });
  });
  });
  Echo.private('answers')
  .listen('AnswerDeletedEvent',(e)=>{
  axios.post('auth/me?mobile=true').then((response)=>{
  this.user = response.data
 axios.get("grades/"+response.data.grade.id+"/notes_from_lesson").then((response)=>{
this.posts = response.data.data;
 });
  });
  });
       }
  }
  </script>
  <style scoped>
  .upload-btn-wrapper {
    position: relative;
    overflow: hidden;
    display: inline-block;
  }

  .btn {
    border: 2px solid gray;
    color: gray;
    background-color: white;
    padding: 8px 20px;
    border-radius: 8px;
    font-size: 20px;
    font-weight: bold;
  }

  .upload-btn-wrapper input[type=file] {
    font-size: 100px;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
  }
  </style>
