import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import NotificationsUser from '@/components/User/Notifications'
import IndexGrade from '@/components/Grade/Index'
import HomeworksGrade from '@/components/Grade/Homeworks'
import NotesFromLessonGrade from '@/components/Grade/NotesFromLesson'
import LayoutGrade from '@/components/Grade/Layout'
import MessagesGrade from '@/components/Grade/Messages'
import AllMessagesGrade from '@/components/Grade/AllMessages'
import ScheduleGrade from '@/components/Grade/Schedule'
import EditUser from '@/components/User/Edit'
Vue.use(Router)

export default new Router({
   mode: 'history',
   routes: [
    {
      path: '/students/',
      name: 'Index',
      component: Login
    },
    {
      path: '/students/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/students/user/students/edit',
      name: 'UserEdit',
      component: EditUser
    },
    {
      path: '/students/user/students/notifications',
      name: 'UserNotifications',
      component: NotificationsUser
    },
    {
      path:'/students/grade',
      name:'GradeLayout',
      component:LayoutGrade,
      children:[
        {
          path: '/students/grade',
          name: 'GradeIndex',
          component: IndexGrade
        },
        {
          path: '/students/grade/students/homeworks',
          name: 'GradeHomeworks',
          component: HomeworksGrade
        },
        {
          path: '/students/grade/students/notes_from_lesson',
          name: 'GradeNotesFromLesson',
          component: NotesFromLessonGrade
        },
        {
          path: '/students/grade/students/messages',
          name: 'GradeMessages',
          component: MessagesGrade
        },
        {
          path: '/students/grade/students/messages/students/:user',
          name: 'GradeMessages',
          component: MessagesGrade
        },
        {
          path: '/students/grade/students/all_messages',
          name: 'GradeAllMessages',
          component: AllMessagesGrade
        },
        {
          path: '/students/grade/students/schedule',
          name: 'GradeSchedule',
          component: ScheduleGrade
        }
      ]
    }
  ]
})
