<template>
<v-app class="background">
<v-toolbar dark color="transparent">
    <v-spacer></v-spacer>
    <v-container grid-list-md text-xs-center style="padding:0px !important;">
<div class="v-toolbar__title ml-0 hidden-xs-only">Student Portal</div>
          </v-container>
    <v-toolbar-items>
     
   </v-toolbar-items>
  </v-toolbar>
  <v-container container style="margin-top:5%;" >
  <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs4>

        </v-flex>
        <v-flex xs4 style="color:white;">
        <div class='headline  text-xs-center' style="margin-bottom:8%;">LOGOWANIE</div>
        <v-alert
      :value="unauthorized"
      type="error"
    >
Bd podczas logowania wprowadzie nie poprawne haso lub login
    </v-alert>
        <v-text-field
        v-model="email"
        label="E-mail"
        required
           solo
         ></v-text-field>
        <v-text-field
        v-model="password"
        label="Haso"
        required
       type="password"
           solo
         ></v-text-field>
              <v-btn block color="primary" dark
                @click="authorize()"
              >
           Zaloguj si
              </v-btn>
              </v-flex>
        <v-flex xs4>

        </v-flex>
        </v-layout>
</v-container>
  </v-container>
  </v-app>
</template>
<script>
  export default {
  data(){
  return {
    email:"",
    password:"",
    unauthorized:false
  }
  },
  created(){
  if(localStorage.getItem('access_token',null) !=null){
     //How is login
     this.$router.push({name:"GradeIndex",props:{}});
  }else{
  axios.post('auth/login',{email:localStorage.getItem("email"),password:localStorage.getItem("password")}).then((response)=>{
  localStorage.setItem("access_token",response.data.access_token);
  this.$router.push({name:"GradeIndex",props:{}});
  })
  }
  },
  methods:{
  authorize(){
axios.post('auth/login',{email:this.email,password:this.password}).then((response)=>{
localStorage.setItem("access_token",response.data.access_token);
localStorage.setItem("email",this.email);
localStorage.setItem("password",this.password);
this.$router.push({name:"GradeIndex",props:{}});
}).catch(()=>{
this.email = "";
this.password = "";
this.unauthorized = true;
});
  }
  }
  }
  </script>
<style scoped>
.background{
  /* The image used */
  background-image: url("/vendor/socialler/img/background.png");

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
</style>
