<template>
<v-app>
       <v-container style="line-height: 70%;">
       <v-container grid-list-xl>
       <v-layout v-bind="binding">
              <v-flex xs12>
<span v-for="message in this.messages" v-bind:id="'m'+message.id" v-if="message.deleted_at==null">
<span v-if="user.id==message.user_id">
<v-flex xs12>
<v-layout v-bind="binding">
<v-flex xs6>
           <v-card dark color="primary"  style="border-top-right-radius: 1.3em;border-top-left-radius: 1.3em;border-bottom-right-radius: 1.3em;border-bottom-left-radius: 1.3em;">
                 <v-card-text @click="DeleteMessage(message)" v-html="message.content"></v-card-text>
                  </v-card>
                  </v-flex>
                  </v-layout>
                  </v-flex>
</span>
<span v-else>
<v-layout>
        <v-flex xs12>
        </v-flex>
                    <v-flex xs12>
                   <v-card dark color="secondary"  style="border-top-right-radius: 1.3em;border-top-left-radius: 1.3em;border-bottom-right-radius: 1.3em;border-bottom-left-radius: 1.3em;">
                            <v-card-text v-html="message.content" ></v-card-text>
                          </v-card>
                          </v-flex>
                          </v-layout>
</span>
</span>
              </v-flex>
              </v-layout>
              </v-container>
              </v-container>
         </v-card>
       </v-flex>
      </v-layout>
      <v-container fluid style="bottom: 0;">
      <v-textarea
      v-model="message.content"
        solo
        label="Tre"
      ></v-textarea>
         <v-btn block color="orange" @click="CreateMessage()" dark>Wylij wiadomo</v-btn>
         </v-card-text>
         </v-card>
         </v-container>
      </v-container>
  </v-app>
</template>
<script>
  export default {
  data(){
  return {
  user:{},
  message:{
  content:""
  },
  messages:[],
  }
  },
  watch:{
  messages:function(){
 setTimeout(()=>{
 this.$vuetify.goTo("#m"+this.messages[this.messages.length-1].id,{
         duration:300,
         offset:100,
         easing:"linear"
       });
 },1);
 }
 },
  methods:{
DeleteMessage(message){
if(confirm("Czy na pewno chcesz usun ?")){
axios.delete('grades/'+this.user.grade.id+"/messagesAlls").then((response)=>{
axios.get('grades/'+this.user.grade.id+"/messagesAlls").then((response)=>{
this.messages = response.data.data.messages_alls;
});
});
}
},
CreateMessage(){
axios.post('messagesAlls',{content:this.message.content,grade_id:this.user.grade.id}).then((response)=>{
this.message.content = "";
axios.get('grades/'+this.user.grade.id+"/messagesAlls").then((response)=>{
this.messages = response.data.data.messages_alls;
});
});
}
  },
     created(){
     let response = [];
    response['data'] = JSON.parse(localStorage.getItem('me'));
     this.user = response.data
     this.messages = response.data.grade.messages_alls;
     },
     mounted(){
     Echo.private('messages_all')
    .listen('MessagesAllCreatedEvent',(e)=>{
    axios.get('grades/'+this.user.grade.id+"/messagesAlls").then((response)=>{
    this.messages = response.data.data.messages_alls;
});
    Echo.private('messages_all')
    .listen('MessagesAllDeletedEvent',(e)=>{
    axios.get('grades/'+this.user.grade.id+"/messagesAlls").then((response)=>{
    this.messages = response.data.data.messages_alls;
});
        //document.querySelector('#message_end').scrollIntoView({
        // behavior: 'smooth'
        //});
      });
    });
     }
  }
  </script>
