<template>
<v-app id="inspire">
  <v-toolbar color="orange" dark  clipped-left id="nav">
    <v-icon @click="$router.push({name:'GradeIndex',params:{}})">keyboard_backspace</v-icon>
    <v-toolbar-title class="white--text">Edytuj uytkownika</v-toolbar-title>
    <v-spacer></v-spacer>
  </v-toolbar>
  <v-content>
  <v-layout row justify-center>
     <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
       <v-card>
         <v-toolbar dark color="orange">
           <v-btn icon dark @click.native="dialog = false">
             <v-icon>close</v-icon>
           </v-btn>
           <v-toolbar-title>Zmie hasa</v-toolbar-title>
         </v-toolbar>
         <v-list three-line subheader>
           <v-subheader></v-subheader>
           <v-container>
           <v-subheader>Wprowad hasa</v-subheader>
             <v-text-field
               label="Nowe haso"
               box
               v-model="forms.password"
             ></v-text-field>
             <v-text-field
               label="Powtrz haso"
               box
                  v-model="forms.password_confirmation"
             ></v-text-field>
             <v-btn orange block @click="UpdatePassword()">Zmie haso</v-btn>
             </v-container>
         </v-list>
       </v-card>
     </v-dialog>
   </v-layout>
   <v-layout row py-4 justify-center>
      <v-flex xs6 md2 >
   <v-card>
   <v-img
           v-bind:src="this.user.avatar"
            aspect-ratio="1"
          ></v-img>
          <v-card-title primary-title >
                   <v-layout row  justify-center>
           <div class="upload-btn-wrapper">
                <v-btn block flat>Dodaj zdjcie</v-btn>
                 <input type="file" name="myfile"  accept="image/*" @change='this.OnFileChangeAvatar'>
                </div>
                </v-layout>
   </v-card-title>
   </v-card>
      </v-flex>
   </v-layout>
   <v-card>
        <v-card-title primary-title text-md-center>
        <v-layout row py-4 justify-center>
           <v-flex xs4 md4 style="opacity: 0.8;">
           Pena nazwa:
           </v-flex>
           <v-flex xs4 md4  @click="UpdateFullName()">
           {{this.user.full_name}}
           </v-flex>
           </v-layout>
           <div style="width: 100%; margin: 30px auto;margin-top:0px;background: rgb(173, 173, 173); height: 2px; opacity: 0.5;"></div>
        </v-card-title>
        <v-card-title primary-title text-md-center>
        <v-layout row py-4 justify-center>
           <v-flex xs4 md4 style="opacity: 0.8;">
           Email:
           </v-flex>
           <v-flex xs4 md4 @click="UpdateEmail()">
           {{this.user.email}}
           </v-flex>
           </v-layout>
             <div style="width: 100%; margin: 30px auto;margin-top:0px;background: rgb(173, 173, 173); height: 2px; opacity: 0.5;"></div>
        </v-card-title>
        <v-card-title primary-title text-md-center>
        <v-layout row py-4 justify-center>
           <v-flex xs12 md12 >
           <v-btn  @click="()=>{this.dialog = true;}" block>Zmie haso</v-btn>
           </v-flex>
           </v-layout>
           <div style="width: 100%; margin: 30px auto; margin-top:0px;background: rgb(173, 173, 173); height: 2px; opacity: 0.5;"></div>
        </v-card-title>
      </v-card>
  </v-content>
</v-app>
</template>
<script>
  export default {
  data(){
return {
user:{},
dialog: false,
forms:{
passsword:null,
password_confirmation:null
}
}
  },
     created(){
     let response = [];
    response['data'] = JSON.parse(localStorage.getItem('me'));
     this.user = response.data
     },
     methods:{
     OnFileChangeAvatar(e){
   let files = e.target.files || e.dataTransfer.files;
     this.createImgAvatar(files[0]);
   },
   createImgAvatar(file){
   var image = new Image;
   var reader = new FileReader;
   reader.onload = (e) => {  this.user.avatar = e.target.result; axios.put('users/'+this.user.id,{full_name:this.user.full_name,email:this.user.email,avatar: e.target.result}).then((response)=>{  });};
   reader.readAsDataURL(file);
   },
   DeleteImagePost(image){
   this.imagesPost.pop(image);
   },
     UpdateEmail(){
    let email = prompt("Email:",this.user.email);
    if(email !=null){
     this.user.email = email;
     localStorage.setItem('me', JSON.stringify(this.user));
     axios.put('users/'+this.user.id,{full_name:this.user.full_name,email:this.user.email}).then((response)=>{

     });
    }
     },
     UpdatePassword(){
    if(this.forms.password == this.forms.password_confirmation){
     axios.put('users/'+this.user.id,{full_name:this.user.full_name,email:this.user.email,password:this.forms.password,password_confirmation:this.forms.password_confirmation}).then((response)=>{
       this.dialog = false;
       this.forms.password = null;
       this.forms.password_confirmation = null;
       localStorage.setItem("password",this.forms.password);
     }).catch((error)=>{
     alert("Bd podczas zmiany hasa");
     this.forms.password = null;
     this.forms.password_confirmation = null;
     });
    }else{
    alert("Oba hasa musz by takie same !!!");
    this.forms.password = null;
    this.forms.password_confirmation = null;
    }
     },
     UpdateFullName(){
        let full_name = prompt("Pena nazwa:",this.user.full_name);
     if(full_name !=null){
     this.user.full_name = full_name;
        localStorage.setItem('me', JSON.stringify(this.user));
     axios.put('users/'+this.user.id,{full_name:this.user.full_name,email:this.user.email}).then((response)=>{

     });
     }
     }
     }
  }
  </script>
<style scoped>
.upload-btn-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.btn {
  border: 2px solid gray;
  color: gray;
  background-color: white;
  padding: 8px 20px;
  border-radius: 8px;
  font-size: 20px;
  font-weight: bold;
}

.upload-btn-wrapper input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
</style>
